  line-height: 95px;
  font-weight: 400;
  border-bottom: 2px solid #000;
  padding-top: 40px;
  padding-bottom: 20px; }

img {
  vertical-align: middle; }

.title {
  font-size: 32px;
  line-height: 48px;
  font-weight: 500;
  margin-bottom: 16px;
  padding-bottom: 6px;
  border-bottom: 1px solid #000; }

